import styled from "@emotion/styled";
import { StatusCodes } from "http-status-codes";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { toast } from "react-toastify";
import './index.css'
import ColorPicker from "./ColorPicker";
import { Color } from "react-color";
import Zoom from "./Zoom";
import CoordsLabel from "./CoordsLabel";
import CanvasService from "../../../../services/canvas/CanvasService";
import { ICanvas } from "../../../../types/canvas/Canvas";
import Canvas from "./Canvas";
import CanvasSocket from "../../../../sockets/CanvasSocket";

export const Edition = () => {
    const [canvas, setCanvas] = useState(null as (ICanvas | null));
    const [selectedColor, setSelectedColor] = useState("#f44336" as Color);
    const [zoomScale, setZoomScale] = useState("80vmin");
    const [activeX, setActiveX] = useState(0);
    const [activeY, setActiveY] = useState(0);
    const { id: canvasId } = useParams();


    CanvasSocket.listenPlacements();


    useEffect(() => {
        if (canvasId) {
            const fetchCanvas = async () => {
                const response = await CanvasService.getCanvas(canvasId);
                if (response.status === StatusCodes.OK) {
                    setCanvas(response.data as ICanvas);
                    CanvasSocket.connect(canvasId);
                } else {
                    toast("" + response.data);
                }
            };

            if (!canvas) {
                fetchCanvas();
            }
        }
    });


    return (
        <Container>
            <ColorPicker selectedColor={selectedColor} setSelectedColor={setSelectedColor} />
            {
                canvas ?
                    <Canvas width={canvas.pixels[0].length} height={canvas.pixels.length} selectedColor={selectedColor} canvasSize={zoomScale}
                        setActiveX={setActiveX} setActiveY={setActiveY} colors={canvas.pixels} /> :
                    null
            }

            <CoordsLabel x={activeX} y={activeY} />
            <Zoom zoomScale={zoomScale} setZoomScale={setZoomScale} />
        </Container>
    );
}

const Container = styled('div')`
    display: flex;
    flex-direction: row;
`
