import { io, Socket } from "socket.io-client";
import { IPlacement } from "../types/canvas/Placement";
import { SERVER_URL } from "../utils/constants";

class CanvasEmitter {
    private static _instance: CanvasEmitter;
    private socket: Socket;

    constructor() {
        this.socket = io(SERVER_URL + '/canvas');
    }

    public static get Instance()
    {
        return this._instance || (this._instance = new this());
    }

    public connect(room: string): void
    {
        this.socket.connect();
        this.socket.emit('join', room);
    }

    public emitPlacement(tile: IPlacement): void
    {
        this.socket.emit('input', tile);
    }

    public listenPlacements(): void
    {
        this.socket.on('placement', (tile: IPlacement) => {
            console.log(tile);
        });

        this.socket.on('ping', (msg) => {console.log(msg)})
    }
}



const CanvasSocket = CanvasEmitter.Instance;

export default CanvasSocket;