import styled from "@emotion/styled";
import { ZoomIn, ZoomOut } from "@material-ui/icons";
import { Slider, Stack } from "@mui/material";
import { Dispatch, SetStateAction } from "react";

interface Props {
    zoomScale: string,
    setZoomScale: Dispatch<SetStateAction<string>>
}


const Zoom = ({ zoomScale, setZoomScale }: Props) => {
    const updateZoomScale = (e: any) => { setZoomScale(e.target.value + "vmin") }

    return (
        <Container>
            <Stack spacing={2} direction="row" sx={{ mb: 1 }} alignItems="center">
                <ZoomOut />
                <Slider aria-label="Volume" min={80} max={300} step={10}
                    value={parseFloat(zoomScale.replace("vmin", ''))} onChange={updateZoomScale} />
                <ZoomIn />
            </Stack>
        </Container>
    )
};

const Container = styled('div')`
    position: fixed;
    bottom: 2em;
    right: 2em;
    width: 15em;
`


export default Zoom;