import styled from "@emotion/styled";
import { Typography } from "@mui/material";
import { Dispatch, SetStateAction } from "react";
import { CirclePicker, Color, ColorChangeHandler } from "react-color";

interface Props {
    selectedColor: Color,
    setSelectedColor: Dispatch<SetStateAction<Color>>
};

const ColorPicker = ({ selectedColor, setSelectedColor }: Props) => {
    const changeColor: ColorChangeHandler = (color) => { setSelectedColor(color.hex) }

    return (
        <Container>
            <Typography variant="h4">Palette</Typography>
            <hr style={{'width': '100%'}} />
            <br />
            <CirclePicker color={selectedColor} onChangeComplete={changeColor} />

        </Container>
    )
};

const Container = styled('div')`
    display: flex;
    flex-direction: column;
    padding-right: 1em;
`

export default ColorPicker;