import "./index.css";
import Pixel from "./../Pixel";
import { Color } from "react-color";
import { Dispatch, SetStateAction } from "react";


interface Props {
    index: number,
    width: number,
    selectedColor: Color,
    setActiveX: Dispatch<SetStateAction<number>>,
    setActiveY: Dispatch<SetStateAction<number>>,
    colors: Array<string>
}

const Row = ({ index, width, selectedColor, setActiveX, setActiveY, colors }: Props) => {
    const pixels = [];

    const handleMouseEnter = () => { setActiveY(index); };

    for (let i = 1; i <= width; i++) {
        pixels.push(<Pixel key={i} index={i} selectedColor={selectedColor} setActiveX={setActiveX} bgColor={colors[i - 1]} />);
    }

    return (
        <div
            className="row"
            onMouseEnter={handleMouseEnter}>
            {pixels}
        </div>);
};


export default Row;