import { Dispatch, SetStateAction } from "react";
import { Color } from "react-color";
import "./index.css";
import Row from "./Row";

interface Props {
    width: number,
    height: number,
    selectedColor: Color,
    canvasSize: string,
    setActiveX: Dispatch<SetStateAction<number>>,
    setActiveY: Dispatch<SetStateAction<number>>,
    colors: Array<Array<string>>
}

const Canvas = ({ width, height, selectedColor, canvasSize, setActiveX, setActiveY, colors }: Props) => {
    let rows = [];

    for (let i = 1; i <= height; i++) {
        rows.push(<Row key={i} index={i} width={width} selectedColor={selectedColor} 
            setActiveX={setActiveX} setActiveY={setActiveY} colors={colors[i - 1]} />);
    }

    return (
        <div id="drawingPanel" style={{width: canvasSize, height: canvasSize}}>
            {rows}
        </div>
    );
};

export default Canvas;