import axios from "axios";
import { SERVER_URL } from "../../utils/constants";

class DrawingController {
    private static _instance: DrawingController;

    public static get Instance()
    {
        return this._instance || (this._instance = new this());
    }

    public async getAllCanvas()
    {
        return await axios.get(SERVER_URL + '/drawing');
    }

    public async getDrawing(id: string)
    {
        return await axios.get(SERVER_URL + '/drawing/' + id)
    }
}



const DrawingService = DrawingController.Instance

export default DrawingService;